.app-wrapper {
  display: grid;
  grid-template-areas:
    "h h "
    "s c ";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  gap: 10px;
}
.app-wrapper-content {
  grid-area: c;
  background-color: forestgreen;
}
